@font-face {
  font-family: Avenir;
  src: url(fonts/Avenir.ttc);
}

* {
  box-sizing: border-box;
}

.search-bar {
  margin-top: 20px;
  border: 1px solid #E1E7ED;
  border-radius: 4px;
  height: 70px;
}

.search-bar--small {
  height: 42px;
}

.search-bar__field {
  width: 100%;
  display: flex;

  color: #3D4E61;
  border: none;
  box-shadow: 0 1px 8px 0 #3D4E611A;

  height: 100%;
  padding-left: 62px;
  background-image: url(images/Search.svg);
  background-position: 26px center;
  background-size: 19px;
  background-repeat: no-repeat;
}

.search-bar__field::placeholder {
  font-family: Avenir, sans-serif;
font-size: 16px;
line-height: 22px;
letter-spacing: 0;
text-align: left;
  height: 19px;
  top: 25px;
  left: 62px;
  padding: 0, 29px, 0, 0;
}

.search-bar__field--small {
  height: 100%;
  padding-left: 33px;
  background-position: 13px center;
  background-size: 11px;
  font-size: 14px;
}

.search-bar__field--small::placeholder {
  font-family: Avenir, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 19.12px;
  letter-spacing: 0;
  text-align: left;
}

.search-bar__field:focus {
  outline: none;
  font-weight: bold;

  background: linear-gradient(180deg, #FFF 0%, #F6F6F7 100%);
}

.search-bar__field--big:focus {
  text-shadow: 0 4px 4px #00000040;
}

.search-bar__field--big:hover {
  box-shadow: 0 4px 4px 0 #00000040;
}

.search-bar__field--small:hover {
  box-shadow: 0 3px 8px 0 #3D4E6133;
}
